body{margin:0 !important;font-family:-apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen', 'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue', sans-serif;-webkit-font-smoothing:antialiased;-moz-osx-font-smoothing:grayscale}code{font-family:source-code-pro, Menlo, Monaco, Consolas, 'Courier New', monospace}

*,body{margin:0 !important;padding:0}

.searchbar{width:350px;min-width:150px}.searchbar .buttons-container{display:flex;flex-direction:row;justify-content:space-evenly}.searchbar .buttons-container .btn.light-blue{border-radius:20px}

.movie-card{display:flex;position:relative;flex-direction:column;height:350px;margin-top:20px;box-shadow:inset -360px 0 90px 0px rgba(0,0,0,0.25);background-size:60%;background-position:top left;border-radius:15px;min-width:350px}@media only screen and (max-width: 600px){.movie-card{background-size:70%;background-position:-30% -30%}}.movie-card .card-buttons .material-icons{margin-right:10px;cursor:pointer}.movie-card .movie-card-actions{position:absolute;left:0;top:0;padding:10px}.movie-card .movie-card-actions .fav-or-watch{font-size:2em}.movie-card .movie-card-actions .fav-or-watch.fav,.movie-card .movie-card-actions .fav-or-watch.watchlist{color:#03a9f4 !important}.movie-card .movie-card-overlay{width:100%;height:100%;border-radius:8px;background:linear-gradient(to right, rgba(42,159,255,0.2) 0%, #212120 60%, #212120 100%);background-blend-mode:multiply;position:absolute;top:0;bottom:0;right:0;left:0}.movie-card .movie-card-right-side{position:absolute;right:0;width:50%;height:100%;padding:10px;overflow:scroll}.movie-card .movie-card-right-side .card-title{font-size:2em;color:#ffffff;margin-bottom:.25em;margin-top:0;opacity:.75}.movie-card .movie-card-right-side .card-detail{text-transform:uppercase;letter-spacing:2px;font-size:.8em;color:#2a9fff;line-height:1;margin:0;font-weight:700;opacity:.5}.movie-card .movie-card-right-side .card-description{display:block;font-weight:300;opacity:.84;margin-bottom:2em;color:white}.movie-card .movie-card-right-side .card-date{display:block;text-transform:uppercase;letter-spacing:2px;font-size:.8em;color:#2a9fff;line-height:1;margin:0;font-weight:700;opacity:.5}

.logo-container{display:flex;justify-content:center;align-items:center;flex-direction:column}.logo-container .face{position:relative;display:flex;width:90px;height:90px;background-color:#537895;background:linear-gradient(-120deg, #4285f4, #34a853, #fbbc05, #ea4335);border-radius:50%;justify-content:center;align-items:center}.logo-container .eyes{position:relative;display:flex;top:-40px}.logo-container .eyes .eye{position:relative;display:block;width:40px;height:40px;background:#fff;margin:50px -1px 0px;border-radius:50%}.logo-container .eyes .eye::before{content:'';position:absolute;top:50%;left:15px;width:20px;height:20px;background-color:#4285f4;border-radius:50%;transform:translate(-50%, -50%)}.logo-container.navbar .face{display:none}.logo-container.navbar .logo{padding:10px;width:auto;height:70px}

.home-wrapper{display:flex;justify-content:center;align-items:center;flex-direction:column;width:100%;height:100vh;margin:0;padding:0}.home-wrapper .welcome-screen{display:flex;max-width:500px;height:50vh;padding:50px 20px;justify-content:center;align-items:center;flex-direction:column}.home-wrapper.top{justify-content:flex-start}.home-wrapper.top .face{display:none}.home-wrapper.top .logo{padding:10px;height:70px;width:auto}.home-wrapper.top .contact{opacity:0;visibility:hidden}

.authorize-image-container{display:flex;position:fixed;top:30px;right:30px;flex-direction:column;align-items:center}@media only screen and (max-width: 600px){.authorize-image-container{top:10px;right:10px}}@media only screen and (max-width: 600px){.authorize-image-container p{display:none}}.authorize-image-container.unauthenticated{cursor:pointer}.authorize-image-container .authorize-image{width:50px;height:50px}@media only screen and (max-width: 600px){.authorize-image-container .authorize-image{width:30px;height:30px}}

.contact{position:absolute;display:flex;padding:20px;justify-content:center;align-items:center;bottom:0;left:0;width:100%;transform:translateY(0px);opacity:1;transition:0.4s}.contact .contact-image{width:50px}

.movie-container .jumbotron{width:100%;height:600px;position:relative;background-repeat:no-repeat;background-size:50%;background-position:top left}@media only screen and (max-width: 600px){.movie-container .jumbotron{background-size:100%}}.movie-container .jumbotron .jumbotron-overlay{width:100%;height:100%;background:linear-gradient(to right, rgba(42,159,255,0.2) 0%, #212120 50%, #212120 100%);background-blend-mode:multiply;position:absolute;top:0px;bottom:0;right:0;left:0}.movie-container .jumbotron .jumbotron-right-side{color:#fff;position:absolute;width:50%;height:100%;right:0;top:0;display:flex;flex-direction:column;justify-content:flex-start;padding:20px 10px 40px;overflow-y:scroll;overflow-x:hidden}@media only screen and (max-width: 600px){.movie-container .jumbotron .jumbotron-right-side{bottom:0;height:100%;left:0;right:0;top:unset;width:100%;text-align:center}}.movie-container .jumbotron .jumbotron-right-side .movie-info{font-size:1em;margin:4px}.movie-container .jumbotron .jumbotron-right-side .title{font-size:3em;line-height:1em;font-weight:bold}.movie-container .jumbotron .jumbotron-right-side .genres{display:inline-flex;flex-wrap:wrap;justify-content:left}@media only screen and (max-width: 600px){.movie-container .jumbotron .jumbotron-right-side .genres{justify-content:center}}.movie-container .jumbotron .jumbotron-right-side .bubble{padding:2px 10px;border-radius:10px;background-color:rgba(42,159,255,0.2);margin:2px}.movie-container .carousel-title{border-bottom:1px solid #212120;color:#212120;display:block;width:100%;text-align:center;margin:20px auto}.movie-container .video-container{width:80%;height:auto;padding-bottom:10px;margin:60px auto;display:flex;justify-content:center;align-items:center;flex-direction:column}.movie-container .video-container .movie-video{position:static;display:inline-flex;height:250px;width:48%;margin:1%}@media only screen and (max-width: 1024px){.movie-container .video-container .movie-video{width:100%;height:400px}}@media only screen and (max-width: 767px){.movie-container .video-container .movie-video{height:200px}}.movie-container .image-container{width:80%;margin:0 auto}.movie-container .image-container .images{display:flex;flex-wrap:wrap;justify-content:center}.movie-container .image-container .movie-posters{width:300px;height:400px;margin:10px}.no-such-movie{margin:50px auto;text-align:center;display:flex;justify-content:center;align-items:center;flex-direction:column}.no-such-movie img{width:40%}

